body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  height: 75vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-main {
  height: 25vh;
  background-color: lightcyan;
  display: flex;
  flex-direction: column;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.content {
  background-color: lightblue;
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

@media only screen and (max-width: 950px) {
  .content {
    justify-content: center;
  }
}

.content #findGithubUser {
  padding: 5px;
  margin: 15px;
  font-size: medium;
  border: none;
}

.content button {
  padding: 10px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  color: white;
  background-color: grey;
  border: none;
  font-size: medium;
}

#result {
  border-radius: 10px;
  background-color: grey;
  width: 50%;
  margin: 10px;
  align-self: center;
  padding: 10px;
  display: flex;
  flex-direction: column;
  color: white;
}

#followers-display {
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-weight: bold;
  flex-wrap: wrap;
  width: 50%px;
  height: 400px;
  padding: 10px;
  overflow-y: scroll;
}

#followers-display img {
  width: 100px;
  height: 100px;
  padding: 10px;
}

#followers-display .user-name {
  font-size: small;
}

.loading {
  opacity: 0.25;
}

.avatar {
  cursor: pointer;
}

.avatar a {
  color: white;
  text-decoration: none;
}

